// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

Vue.config.productionTip = false


/* Import the firebase SDK and extend with firestore */

/* Bind firebase to your Vue instance  把firebase绑定在Vue原型上，让其他页面可以直接调用*/

// Vue.prototype.$firebase = firebase;

/* eslint-disable no-new */

import fbAuth from './components/firebaseinitAuth'

let fbAuth2 = fbAuth.auth();
let app;

fbAuth2.onAuthStateChanged((user) => {
    if (!app) {
        // let app;
        /* eslint-disable no-new */
        app = new Vue({
            el: '#app',
            render: h => h(App),
            router,

            // 这里的store要注册到 app组件里面后全局才能被调用
            // store,
            components: {
                App
            },
            template: '<App/>'
        })
    }
})


