<template>
  <div class="hello">
    <h1>System List</h1>
    <ul>
      <li v-for="item in testCollection" :key="item.key">{{item}}</li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'SystemList',
    data() {
      return {
        testCollection: []
      }
    },
    mounted() {
      // 连接firestore数据库
      const db = this.$firebase.firestore();

      const settings = { /* your settings... */
        timestampsInSnapshots: true
      };
      db.settings(settings);

      // 读取指定的集合下面的数据
      db.collection('test').get().then(snap => {

        // console.log('返回的数据：', snap);

        const testCollection = [];
        snap.forEach(doc => {
          testCollection.push({
            [doc.id]: doc.data()
          })
        });

        // console.log('处理后的数据：', testCollection)
        this.testCollection = testCollection;
      })
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1,
  h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

</style>



