<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>Employees</h4>
        <h5 v-show="loading">loading...</h5>
      </li>
      <li v-for="employee in employees" :key="employee.id" class="collection-item">
        <div class="chip">{{employee.dept}}</div>
        {{employee.employee_id}}:{{employee.name}}

        <router-link :to="{name:'view-employee',params:{employee_id:employee.employee_id}}" class="secondary-content">
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>
    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>

</template>

<script>
  import firebaseApp from './firebaseinitAuth.js'
  // import firebase from 'firebase'

  let db = firebaseApp.firestore();
  // const db = firebase.firestore();

  export default {
    name: 'dashboard',
    data() {
      return {
        employees: [],
        loading: true
      }
    },
    created() {
      // 连接firestore数据库
      // const db = this.$firebase.firestore();

      const settings = { /* your settings... */
        timestampsInSnapshots: true
      };
      db.settings(settings);

      // 获取 employees下面的数据
      db.collection('employees').get().then(querySnapshot => {

        querySnapshot.forEach((doc) => {
          this.loading = false;
          const data = {
            'id': doc.id,
            'employee_id': doc.data().employee_id,
            'name': doc.data().name,
            'dept': doc.data().dept,
            'position': doc.data().position
          }
          this.employees.push(data);
        })
      })
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1,
  h2 {
    font-weight: normal;
  }

  /* ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  } */

</style>



