<template>
  <div id="view-employee">
    <ul class="collection with-header">
      <li class="collection-header">
        <h5 v-show="loading">loading...</h5>
        <h4>{{name}}</h4>
      </li>
      <li class="collection-item">Employee_id#:{{employee_id}}</li>
      <li class="collection-item">Departement#:{{dept}}</li>
      <li class="collection-item">Position#:{{position}}</li>
    </ul>

    <router-link to="/" class="btn gray">Back</router-link>
    <button class="btn red" @click="deleteEmployee">Delete</button>

    <div class="fixed-action-btn">
      <router-link :to="{name:'edit-employee',employee_id:employee_id}" class="btn-floating btn-large red">
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
  import db from './firebaseinit.js'
  const settings = { /* your settings... */
    timestampsInSnapshots: true
  };
  db.settings(settings);

  export default {
    name: 'view-employee',
    data() {
      return {
        employee_id: null,
        name: null,
        dept: null,
        position: null,
        loading: true
      }
    },
    // 在路由进来前执行
    beforeRouteEnter(to, from, next) {
      // 通过 where 来进行搜索查询，通过get()方法来获取数据，then进行回调数据处理
      db.collection('employees').where('employee_id', '==', to.params.employee_id).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.employee_id = doc.data().employee_id;
            vm.name = doc.data().name;
            vm.dept = doc.data().dept;
            vm.position = doc.data().position;
            vm.loading = false;
          })
        })
      })
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData() {
        // console.log('执行 fetchData')
        db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.employee_id = doc.data().employee_id
            this.name = doc.data().name
            this.dept = doc.data().dept
            this.position = doc.data().position
          })
        })
      },
      deleteEmployee() {
        if (confirm('Are you sure?')) {
          db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(
            querySnapshot => {
              querySnapshot.forEach(doc => {
                // 通过 doc.ref 来对当前帖子进行删除
                doc.ref.delete();
                // $route.params用来获取参数  $router用来跳转引用
                this.$router.push('/')
              })
            })
        }
      }
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1,
  h2 {
    font-weight: normal;
  }

  /* ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  } */

  /* a {
    color: #42b983;
  } */

</style>



