<template>
  <div id="edit-employee">
    <h3>Edit employee</h3>
    <div class="row">
      <form @submit.prevent="updateEmployee" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input disabled type="text" v-model="employee_id">
            <!-- <label>Employee ID#</label> -->
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" required>
            <!-- <label>Name</label> -->
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="dept" required>
            <!-- <label>Departement</label> -->
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="position" required>
            <!-- <label>Position</label> -->
          </div>
        </div>

        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>

    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
  import db from './firebaseinit.js';

  export default {
    name: 'edit-employee',
    data() {
      return {
        employee_id: null,
        name: null,
        dept: null,
        position: null
      }
    },
    // 在路由进来前执行
    beforeRouteEnter(to, from, next) {
      // 通过 where 来进行搜索查询，通过get()方法来获取数据，then进行回调数据处理
      db.collection('employees').where('employee_id', '==', to.params.employee_id).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.employee_id = doc.data().employee_id;
            vm.name = doc.data().name;
            vm.dept = doc.data().dept;
            vm.position = doc.data().position;
          })
        })
      })
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData() {
        // console.log('执行 fetchData')
        db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.employee_id = doc.data().employee_id
            this.name = doc.data().name
            this.dept = doc.data().dept
            this.position = doc.data().position
          })
        })
      },
      updateEmployee() {
        db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
            // 更新数据
            doc.ref.update({
              employee_id: this.employee_id,
              name: this.name,
              dept: this.dept,
              position: this.position
            }).then(() => {
              this.$router.push({
                name: 'view-employee',
                params: {
                  employee_id: this.employee_id
                }
              })
            })
          })
        })
      }
    }

  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1,
  h2 {
    font-weight: normal;
  }

  /* ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  } */

</style>



